import { defineComponent, ref, onBeforeMount } from 'vue'
import VueApexCharts from 'vue3-apexcharts'
import ServiceEngagement from '@/service/ServiceEngagement/ServiceEngagement'

export default defineComponent({
  name: 'EngagementByStatus',
  inheritAttrs: false,
  emit: ['chartData'],
  components: {
    apexchart: VueApexCharts
  },
  props: {
    chartSetting: {
      default: {}
    },
    showTitle: {
      type: Boolean,
      default: true
    },
    dateObj: {
      default: {}
    }
  },
  data () {
    return {
      show: false,
      istabledrawer: false
    }
  },
  setup (props, { emit }) {
    const loader = ref(false)
    const seetingArray = ref([])
    const chartOptions = ref({}) as any
    const data = ref([])
    const showChart = ref(false)
    const settings = ref(props.chartSetting) as any
    const postData = ref({})
    const currentTabelRes = ref('')
    const filterData = ref([])
    const engFilter = ref([]) as any
    const selectedDateObj = ref(props.dateObj) as any;
    chartOptions.value = {
      plotOptions: {
        donut: {
          labels: {
            show: true,
            name: {
              show: true,
              fontSize: '16px',
              fontFamily: 'Helvetica, Arial, sans-serif',
              fontWeight: 400,
              color: '#373d3f',
              offsetY: 16,
              formatter: function (val : any) {
                return val
              }
            },
            value: {
              show: true,
              fontSize: '16px',
              fontFamily: 'Helvetica, Arial, sans-serif',
              fontWeight: 400,
              color: '#373d3f',
              offsetY: 16,
              formatter: function (val : any) {
                return val
              }
            },
            total: {
              show: true,
              label: 'Total',
              fontSize: '22px',
              fontFamily: 'Helvetica, Arial, sans-serif',
              fontWeight: 600,
              color: '#373d3f'
            }
          }
        }
      },
      chart: {
        type: 'donut',
        width: 600,
        events: {
          click: function (event : any, chartContext : any, config : any) {
            console.log('Click Event', event)
            console.log('Click chartContext', chartContext)
            console.log('Click config', config)
            // The last parameter config contains additional information like `seriesIndex` and `dataPointIndex` for cartesian charts
          }
        }
      },
      legend: {
        show: false,
        position: 'top',
        horizontalAlign: 'center'
        // width: 500
      },
      dataLabels: {
        textAnchor: 'start'
      },
      title: {
        text: '',
        align: 'center',
        offsetX: 0,
        offsetY: 0,
        floating: false,
        style: {
          fontSize: '16px',
          fontFamily: 'Mulish',
          fontWeight: '600',
          color: '#1B2559'
        }
      },
      colors: [] as any,
      stroke: {
        show: false
      },
      labels: ['Pending', 'Accepted', 'Declined', 'Completed', 'Cancelled'],
      responsive: [{
        breakpoint: 480,
        options: {
          chart: {
            width: 300
          }
        }
      }]
    }
    onBeforeMount(async () => {
      loader.value = true
      await getChartData()
      loader.value = false
      initFilter()
    })
    const initFilter = async () => {
      engFilter.value = [
        {
          id: '1',
          'filter-name': 'engagement-type-list',
          title: 'Engagement Type ',
          filterLabel: [],
          chooseNames: [],
          chooseId: [],
          filterKey: 'type'
        },
        {
          id: '2',
          'filter-name': 'product-list',
          title: 'Product',
          filterLabel: [],
          chooseNames: [],
          chooseId: [],
          filterKey: 'product'
        }
      ]
    }
    const getChartData = async () => {
      const url = settings.value.endPoint
      const postData = {
        fromtime: selectedDateObj.value.starttime,
        totime: selectedDateObj.value.endtime
      }
      const res = await ServiceEngagement.commonEndpoint('post', url, postData)
      if (res.apiStatus) {
        chartOptions.value.colors = res.result.colors
        chartOptions.value.labels = res.result.labels
        data.value = res.result.data
        currentTabelRes.value = res.chart_data.end_point
        showChart.value = true
      }
      console.log('-------- GetChartData For EngagementByStatus ---------', res)
    }
    const chartClick = async (event: any, chartContext: any, config: any) => {
      if (event) {
        const selectedIndex = config.dataPointIndex
        const selectedLabel = chartOptions.value.labels[selectedIndex]
        const data = {
          selectedLabel: selectedLabel,
          chartTittle: settings.value.title,
          postData: postData.value,
          chartSetting: settings.value,
          tabelEndpoint: currentTabelRes.value,
          tableFilterData: engFilter.value
        }
        emit('chartData', data)
        console.log('--- Selected Chart Labels ---', chartOptions.value.labels[selectedIndex])
      }
    }

    return {
      loader,
      getChartData,
      seetingArray,
      chartOptions,
      data,
      showChart,
      chartClick,
      filterData,
      initFilter,
      engFilter,
      selectedDateObj
    }
  }

})
